import { LocationWithState } from '../types';

export default function locationToRelativePath(location: LocationWithState, siteUrl: string): String {
  const { hostname, pathname, href } = location;
  if (hostname === 'localhost') {
    return pathname;
  } else if (pathname === `${siteUrl}404/`) {
    return '/404/';
  } else {
    return href.replace(siteUrl.slice(0, -1), '');
  }
}
